// material
import { Container, Typography, Button, Grid, Divider, Stack, Box, DialogContent } from '@mui/material';
import { motion } from 'framer-motion';
import { styled, useTheme  } from '@mui/material/styles';
import { Parallax } from 'react-parallax';
import { useState, useEffect } from 'react';
import Lottie from 'react-lottie';
import useSound from 'use-sound';

// hooks
import useSettings from '../hooks/useSettings';
import { fCurrency, fCRCurrency } from '../utils/formatNumber';

// components
import Page from '../components/Page';
import { varWrapEnter, varFadeInUp, DialogAnimate, varZoomInUp } from '../components/animate';

// assets
import SuccessCheckLottie from '../assets/lottie/successCheck.json';
import FailedLottie from '../assets/lottie/failed.json';
import popDingSound from '../assets/sfx/popDing.wav';

import useClaims from '../hooks/useClaims';
import { utils } from 'ethers';

import { useEthers,  useCalls, useContractFunction} from '@usedapp/core';
import { DISTRIBUTER_VESTED_CONTRACT, MERKLE_VESTED_DISTRIBUTER_ADDRESS } from '../config';

// ----------------------------------------------------------------------

const AnimateContainer = styled(motion.div)(({ theme }) => ({

}));

const successCheckOptions = {
  loop: false,
  autoplay: true,
  animationData: SuccessCheckLottie,
  background: 'transparent'
};

const failedOptions = {
  loop: false,
  autoplay: true,
  animationData: FailedLottie,
  background: 'transparent'
};

export default function AirdropPage() {

  const { account } = useEthers();

  const data = useClaims('snch');

  const [claimMessageValue, setClaimMessageValue] = useState('0')
  const [claimValue, setClaimValue] = useState('0');

  const [rewardsValue, setRewardsValue] = useState('0')

  const [claimedValue, setClaimedValue] = useState('0')

  const [isClaiming, setIsClaiming] = useState(false);

  const [playPopDingSound, { stopPopDingSound }] = useSound(popDingSound);

  useEffect(() => {
    if (data && data.amount !== '') {

      let amount = utils.parseEther(utils.formatEther(data.amount.toString()));

      setRewardsValue(utils.formatEther(amount));
    }
  }, [data, account]);

  const [claimable, claimed] = useCalls([
    {
    contract: DISTRIBUTER_VESTED_CONTRACT,
    address: MERKLE_VESTED_DISTRIBUTER_ADDRESS ,
    method: 'getClaimableAmount',
    args: [data.index, account, data.amount, data.proof]
    },
    {
      contract: DISTRIBUTER_VESTED_CONTRACT,
      address: MERKLE_VESTED_DISTRIBUTER_ADDRESS ,
      method: 'claimed',
      args: [account]
      },
  ]);


  useEffect(() => {
    if (claimable && claimable.value) {

      let amount = utils.parseEther(utils.formatEther(claimable.value[0].toString()));

      setClaimValue(utils.formatEther(amount));
    }

    if (claimed && claimed.value) {

      let amount = utils.parseEther(utils.formatEther(claimed.value[0].toString()));

      setClaimedValue(utils.formatEther(amount));
    }


  }, [claimable, claimed, account]);


  console.log(claimable)
  // STATUS DIALOG
  const [openStatusDialog, setOpenStatusDialog] = useState({ open: false, success: false });
  const [statusDialogMsg, setStatusDialogMsg] = useState('');
  const [statusDialogAmt, setStatusDialogAmt] = useState(0);
  const [failedStatusMsg, setFailedStatusMsg] = useState('');

  const handleOpenSuccessDialog = (msg, amount) => {
    playPopDingSound();
    setStatusDialogMsg(msg);
    setStatusDialogAmt(amount);
    setOpenStatusDialog({ open: true, success: true });
  }

  const handleOpenFailedDialog = (msg, amount, reason) => {
    playPopDingSound();
    setStatusDialogMsg(msg);
    setStatusDialogAmt(amount);
    setOpenStatusDialog({ open: true, success: false });
    setFailedStatusMsg(reason);
  }

  const handleCloseStatusDialog = () => {
    setOpenStatusDialog({ ...openStatusDialog, open: false });
    setStatusDialogMsg('');
    setStatusDialogAmt(0);
  }

  const { state, send } = useContractFunction(DISTRIBUTER_VESTED_CONTRACT, 'claim', {
    transactionName: 'Claim Aidrop SHAZU'
  });

  useEffect(() => {
    if (state.status === 'Exception') {
      setIsClaiming(false);

      console.log(state.errorMessage)
      handleOpenFailedDialog('Airdrop Claim Failed', claimValue, state.errorMessage);
    }

    if (state.status === 'Fail') {
      setIsClaiming(false);
      handleOpenFailedDialog('Airdrop Claim Failed', claimValue, state.errorMessage);
    }

    if (state.status === 'None') {
      setIsClaiming(false);
    }

    if (state.status === 'Success') {
      setIsClaiming(false);
      handleOpenSuccessDialog('Airdrop Claim Success', claimMessageValue);
    }
  }, [state, account]);

  const handleClaim = () => {
    setIsClaiming(true);
    setClaimMessageValue(claimValue);
    send(data.index, account, data.amount, data.proof);
  }


  return (
    <Page title="SHAZU - Token" style={{ overflow: 'hidden'}}>
      <AnimateContainer initial="initial" animate="animate" variants={varWrapEnter}>
        <section>
          <Parallax blur={{ min: -10, max: 40 }} strength={1000} 
            style={{ position: 'absolute', width: '100%', height: '100%', zIndex: '-1' }} 
            bgImage="/static/SHAZU/background/gradient-before-2.svg"
            bgImageAlt=""
          />
          <Container maxWidth='xl'>
            <Grid container spacing={2} alignItems="stretch" direction="row" justifyContent="flex-start" style={{ paddingTop: '4rem' }}>
              <Grid item xs={12} md={12} lg={6} xl={6}> 
              <motion.div variants={varFadeInUp}>
                  <Stack className="shazu-coin-stack-container floating" style={{ marginTop: '-2rem' }}>
                    <img src="/static/SHAZU/misc/parachute.png" alt="" style={{ width: '70%', maxWidth: '20rem', margin: 'auto' }} />
                    <div style={{ position: 'relative', width: '100%', display: 'flex', justifyContent: 'center' }}>
                      <img className="floating2" src="/static/SHAZU/misc/coin1.png" alt="" style={{ width: '4rem', position: 'absolute', marginTop: '-4rem', marginLeft: '-4rem' }} />
                      <img className="floating3" src="/static/SHAZU/misc/coin2.png" alt="" style={{ width: '4rem', position: 'absolute', marginTop: '-4rem', marginRight: '-4rem' }} />
                      <img className="floating4" src="/static/SHAZU/misc/coin3.png" alt="" style={{ width: '4rem', position: 'absolute', marginTop: '-6rem', marginLeft: '-6rem' }} />
                      <img className="floating2" src="/static/SHAZU/misc/coin1.png" alt="" style={{ width: '4rem', position: 'absolute', marginTop: '-10rem', marginRight: '-8rem' }} />
                    </div>
                  </Stack>
                </motion.div>
              </Grid>

              <Grid item xs={12} md={12} lg={6} xl={6} style={{ display: 'flex'}}> 
                <Stack style={{ width: '100%' }}>
                  <motion.div variants={varFadeInUp}>
                    <Stack>
                      <div className="gradient-card left-gradient-bar" style={{ width: 'fit-content', padding: '1rem' }}>
                        <Typography className="neo-font" style={{ lineHeight: '1', marginBottom: '0.4rem', fontSize: '3rem' }}>
                          SHAZU <span className="yellow-color">Airdrop</span>
                        </Typography>
                        <div>
                          For all participants who minted SNC NFT will be able to claim their airdrop below which will be vested for 12 weeks with weekly unlocks.
                        </div>
                      </div>
                    </Stack>
                  </motion.div>

                  <motion.div variants={varFadeInUp} style={{ marginTop: '2rem' }}>
                    <Box className="gradient-border gradient-border-red" 
                      style={{ width: '100%', padding: '0.1rem', margin: '0.4rem', maxWidth: '45rem', marginBottom: '10rem', marginInline: 'auto' }}
                    >
                      <div className="shadow-sm" 
                        style={{ backgroundColor: '#141416', padding: '1.4rem 1rem', borderRadius: '8px', marginBottom: '1rem', backgroundImage: 'url(/static/SHAZU/snc/snc-card-bg.jpg)', backgroundPosition: 'center' }}
                      >
                        <Typography variant="h3">
                          Claim your SHAZU Airdrop!
                        </Typography>

                        <Divider style={{ marginTop: '0.4rem', marginBottom: '1rem'}} />
                        <div className="elevate-card-sm" style={{ marginTop: '0.4rem', padding: '0.6rem' }}>
                          <div>
                            Rewards
                          </div>
                          <div className="bold-yellow" style={{ fontSize: '1.6rem', marginTop: '-0.4rem' }}>
                            {fCRCurrency(rewardsValue)} SHAZU
                          </div>
                        </div>
                        <div className="elevate-card-sm" style={{ marginTop: '0.4rem', padding: '0.6rem' }}>
                          <div>
                            Claimed
                          </div>
                          <div className="bold-yellow" style={{ fontSize: '1.6rem', marginTop: '-0.4rem' }}>
                            {fCRCurrency(claimedValue)} SHAZU
                          </div>
                        </div>
                        <div className="elevate-card-sm" style={{ marginTop: '0.4rem', padding: '0.6rem' }}>
                          <div>
                            Available to claim
                          </div>
                          <div className="bold-yellow" style={{ fontSize: '1.6rem', marginTop: '-0.4rem' }}>
                            {fCRCurrency(claimValue)} SHAZU
                          </div>
                        </div>

                        {/* disable if claim amount is 0 */}
                        <Button
                          onClick={handleClaim}
                          disabled={isClaiming || utils.parseEther(claimValue).eq('0')}
                          variant="contained" color="yellow" size="large" style={{ width: '100%', marginTop: '2rem' }}
                        >
                        {isClaiming ? <>CLAIMING ...</> : <>CLAIM</>}
                        </Button>
                      </div>
                    </Box>
                  </motion.div>
                </Stack>
              </Grid>

            </Grid>
          </Container>
        </section>
      </AnimateContainer>

      {/* STATUS DIALOG */}
      <DialogAnimate open={openStatusDialog.open} maxWidth="xs" animate={varZoomInUp} scroll="paper" onClose={handleCloseStatusDialog}>
        <DialogContent style={{ textAlign: 'center' }}>
          {
            openStatusDialog.success 
            ?
            <div className="successCheck-lottie-container">
              <Lottie options={successCheckOptions} isClickToPauseDisabled />
            </div>
            :
            <div className="successCheck-lottie-container">
              <Lottie options={failedOptions} isClickToPauseDisabled />
            </div>
          }
          
          <Typography variant="h3">
            {statusDialogMsg}
          </Typography>
          
          {
            openStatusDialog.success 
            ?
            <div className="elevate-card">
              <Typography variant="h5">
                {fCRCurrency(statusDialogAmt)} SHAZU
              </Typography>
            </div>
            :
            <div>
              {failedStatusMsg}
            </div>
          }
        </DialogContent>
      </DialogAnimate>
    </Page>
  );
}
